<template>

  <div class="carousel">
    <div class="carousel-inner">
      <img
        :src="topImg"
        class="w-100"
        style="height: 500px; object-fit: cover;"
      />
      <div class="carousel-caption py-5 px-5" style="right: 0%; left: 0%; bottom: 0%; background-color: rgba(1,1,1,0.7);">
        <h1 v-if="!brand">¡Año nuevo, TV nueva!</h1>
        <h2 v-if="!brand">Las mejores teles de 2021 para ayudarte a olvidar 2020</h2>
        <h1 v-if="brand">{{brand}}</h1>
      </div>
    </div>
  </div>

  <div class="container text-justify mt-2 mt-md-4">

    <div class="jumbotron my-3 py-3">
      <div class="row ml-4">
        <input class="form-check-input" type="checkbox" id="gridCheck1" v-model="applyFilters">
        <label class="form-check-label" for="gridCheck1">
          Aplicar filtros de búsqueda
        </label>
      </div>

      <div v-if="applyFilters" class="row mt-4 p-1">
        <div class="col-12 col-md-6">
          <label for="customRange1">Desde - {{priceFrom}} €</label>
          <input id="customRange1" type="range" class="custom-range" :min="minPrice" :max="priceTo" v-model="priceFrom">
        </div>
        <div class="col-12 col-md-6">
          <label for="customRange2">Hasta - {{priceTo}} €</label>
          <input id="customRange2" type="range" class="custom-range" :min="priceFrom" :max="maxPrice" v-model="priceTo">
        </div>

        <div class="col-12 col-md-6 pl-5 mt-3">
          <input class="form-check-input" type="checkbox" v-model="priceMovedDown">
          <label class="form-check-label" for="gridCheck2">
            Ha bajado de precio
          </label>
        </div>
      </div>
    </div>

    <!--div class="col-12 col-md-6 dropdown px-4 mt-3">
      <input type="text" class="w-100 p-2 m-0" placeholder="Palabras clave" v-model="userInput" />
      <div class="dropdown-menu show w-100 p-0 m-0" style="max-height: 200px; overflow: scroll;">
        <span class="dropdown-item text-truncate" role="button" v-for="name of getItems" :key="name">{{name}}</span>
      </div>
    </div-->

    <div class="my-5 row justify-content-center">
      <iframe style="height:240px;" scrolling="no" frameborder="0" :src="iFrameUrl" class="col-6 col-md-4 col-lg-3 my-2 pl-5" v-for="({ itemId, iFrameUrl }) of itemsToShow" :key="itemId" />
    </div>

    <!-- PAGINATION -->
    <ul v-if="endPage > startPage" class="pagination justify-content-center mb-5 pb-5" style="cursor: pointer;">
      <li class="page-item" v-bind:class="currentPage == startPage ? 'active':''" @click="currentPage = startPage">
        <a class="page-link"> {{startPage}}</a>
      </li>
      <li class="page-item" v-bind:class="currentPage == page ? 'active':''" v-for="(page, index) in midPages" :key="index" @click="currentPage = page">
        <a class="page-link">{{page}}</a>
      </li>
      <li class="page-item" v-bind:class="currentPage == endPage ? 'active':''" @click="currentPage = endPage">
        <a class="page-link">{{endPage}}</a>
      </li>
    </ul>

  </div>

</template>

<script lang="ts">
import { computed, defineComponent, inject, reactive, toRefs } from 'vue'
import { ShopItem } from '@shared/components/types/ShopItem'
import paginate from '@shared/components/paginate'
import { getRandomFromArr } from '@shared/components/array_utils'
import { onBeforeRouteUpdate } from 'vue-router'

export default defineComponent({
  props: {
    brand: String
  },
  setup (props) {
    onBeforeRouteUpdate(async (from, to, next) => {
      await next()
      location.reload()
    })
    const items: ShopItem[] = props.brand ? Object.values<ShopItem>(inject('items', {})).filter(({ brand }) => brand === props.brand) : Object.values<ShopItem>(inject('items', {}))

    const prices = items.map(({ price }) => price)
    const minPrice = Math.floor(Math.min(...prices))
    const maxPrice = Math.floor(Math.max(...prices))

    const state = reactive({
      minPrice,
      maxPrice,
      priceFrom: minPrice,
      priceTo: maxPrice,
      applyFilters: false,
      priceMovedDown: false,
      currentPage: 1,
      startPage: 1,
      endPage: 1,
      midPages: [1]
    })

    const itemsAfterFilters = computed(() => {
      if (!state.applyFilters) return items
      const itemsAfterPriceFilter = items.filter(({ price }) => Math.floor(price) >= Math.floor(state.priceFrom) && Math.floor(price) <= Math.floor(state.priceTo))
      if (state.priceMovedDown) {
        return itemsAfterPriceFilter.filter(({ oldPrice, price }) => oldPrice && oldPrice > price)
      }
      return itemsAfterPriceFilter
    })

    const itemsToShow = computed(() => {
      const { startPage, endPage, startIndex, endIndex } = paginate(itemsAfterFilters.value.length, state.currentPage, 8)
      state.startPage = startPage
      const prev = state.currentPage - 1 >= startPage ? state.currentPage - 1 : 0
      const next = state.currentPage + 1 <= endPage ? state.currentPage + 1 : 0
      state.midPages = [prev, state.currentPage, next].filter(p => p > startPage && p < endPage)
      state.endPage = endPage
      return itemsAfterFilters.value.slice(startIndex, endIndex + 1)
    })

    const topImg = `raw_imgs/bg${getRandomFromArr([1, 2, 3])}.jpg`

    // const names = items.map(({ name }) => name)
    // const userInput = ref('')
    // const getItems = computed(() => names.filter(r => r.trim().toLowerCase().startsWith(userInput.value.trim().toLowerCase())))/* .slice(0, 5)) */

    // const acChanged = (val: string) => {
    //   console.log({ val })
    // }

    return { itemsToShow, ...toRefs(state), topImg }
  }
})
</script>

